}

.our-clients-wrap {
    float: none;
    margin-top: 10px;
    position: relative;
}
ul.our-clients li {
    list-style: none;
    width: 20%;
    padding: 10px 15px;
    text-align: center;
    float: left;
    min-height: 130px;
    margin: 0;
    vertical-align: middle;
    border-bottom: 1px solid #e9e9e9;
    border-right: 1px solid #e9e9e9;
